import { request } from 'utils';
import { CLIENT_RENEG_LIMIT } from 'tls';


request.prefix = '/api/categories';

export const getListTown = (page = 1,IsPagingEnabled = true) => request({
  url: '/Town',
  params: {
    page,
    IsPagingEnabled,
  },
  method: 'GET',
})
export const getTown = (id) => request({
  url: `/Town/${id}`,
  method: 'GET',
})
export const deleteTown = (id) => request({
  url: `/Town/${id}`,
  method: 'DELETE',
})
export const updateTown = (id, values) => {
  console.log('value', values)
  return request({
    url: `/Town/${id}`,
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
    },
    method: 'PUT',
  })
}
export const createTown = (values) => {
  console.log('valuesvaluesvalues', values)
  return request({
    url: '/Town',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
    },
    method: 'POST',
  })
} 


